@import "../../../data/styles.css";

.card {
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
    margin-left: 1rem;
	mix-blend-mode: normal;
	border-radius: 20px;
	outline: 3px solid #f4f4f5;
}

.card-container {
	padding: 30px;
	padding-bottom: 5px;
}

.card-header {
	display: flex;
	align-items: center;
}

.card-icon {
	font-size: 15px;
	color: var(--tertiary-color);
}

.card-title {
	color: var(--secondary-color);
	font-size: 14px;
	padding-left: 20px;
	font-weight: 650;
}

.card-body {
	padding-top: 40px;
	font-size: 15px;
}
