@import "../../../data/styles.css";

.nav-container {
	margin: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}

.navbar {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	top: 3vh;
	z-index: 999;
}

.nav-background {
	width: 380px;
	height: 50px;
	padding-left: 0%;
	padding-right: 0%;
    /*background: linear-gradient(135deg, #6366F1, #22D3EE);*/
    background-color: white;
	box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
	border-radius: 40px;
}

.nav-list {
	display: flex;
	justify-content: space-between;
	list-style: none;
	align-items: center;
	margin-left: -16px;
	margin-right: 20px;
	margin-top: 11px;
}

.nav-item {
	font-weight: bold;
	font-size: 90%;
    padding: 0.2rem 0.5rem;
    border-radius: 15px;
}

.nav-item.active {
    /*color: black !important;*/
    background-color: #f8e597;

    /*color: var(--link-color) !important;*/
}


.nav-item.active a {
    /*color: black !important;*/

	/*color: var(--link-color) !important;*/

}

.nav-item a {
	text-decoration: none;
	/*color: var(--primary-color);*/
	color: black;
}

.nav-item a:hover {
	/*color: var(--link-color);*/
	transition: color 0.3s ease-in-out;
}

@media (max-width: 600px) {
	.navbar {
		width: 100%;
		font-size: 70%;
        position: fixed;
	}

	.nav-background {
		width: 90%;
		height: 50px;
	}

	.nav-item {
		font-weight: bold;
        padding: 0.5rem 0.2rem;
	}
}
