.projects-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 25px;
}

.projects-logo {
	display: flex;
	position: fixed;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	top: 4vh;
	z-index: 999;
}

.carousel-slide {
    display: flex;
    justify-content: center;
    align-items: center;
}

.carousel-image {
    max-width: 300px;
    max-height: 800px; /* nie wychodzi poza modal */
    object-fit: contain; /* zachowuje proporcje screenshotów telefonu */
    border-radius: 0.5rem; /* lekko zaokrąglone rogi */
}


.fitnessAppModal-title {
    text-align: center;
    font-size: 1.5rem;
}

.custom-carousel {
    padding-bottom: 3rem;
    max-width: 300px;
    margin: 0 auto;
    .slick-arrow {
        color: black;
        font-weight: bold;
        &.slick-prev {
            left: -2rem;
        }
        &.slick-next {
            right: -1rem;
        }


        &::after {
            height: 30px;
            width: 30px;
            border-inline-start-width:10px;
            border-block-start-width:10px;
        }
    }
}


@media (max-width: 500px) {
    .carousel-image {
        max-width: 200px;
        max-height: 534px;
    }

    .custom-carousel {
        max-width: 200px;
    }

    .fitnessAppModal-title {
        font-size: 1rem;
    }
}



@media (max-width: 800px) {
    .projects-logo {
        visibility: hidden;
    }
}

.projects-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 120px;
}

@media (max-width: 600px) {
	.projects-container {
		padding-top: 60px;
	}

	.projects-project {
		width: calc(100% / 2);
	}
}
